import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import { ethers } from 'ethers';
import "./staking.css";

import TokenArtifact from '../../contracts/tokenContract.json';
import StakingArtifact from '../../contracts/stakingContract.json' 
import { smartContract } from '../../address-book.js'

let staking30Contract
let staking90Contract
let staking365Contract
let approveAmount
let approveBtn
let withdrawBtn
let emergencyWithdrawBtn
let legioBalance
let stakingAPY
let stakingPeriod = -1
let signAddress
console.log('------------> reset?')
let userStaked

const initialStaking = async () => {
  console.log('Staking page start')
  try {
    const { ethereum } = window;
    if (ethereum) {
      const provider = new ethers.providers.Web3Provider(ethereum)
      const signer = provider.getSigner()
      staking30Contract = new ethers.Contract(smartContract.staking30, StakingArtifact.abi, provider)
      console.log('[initialStaking] stak30:', staking30Contract.address)
      staking90Contract = new ethers.Contract(smartContract.staking90, StakingArtifact.abi, provider)
      console.log('[initialStaking] stak90:', staking90Contract.address)
      staking365Contract = new ethers.Contract(smartContract.staking365, StakingArtifact.abi, provider)
      console.log('[initialStaking] stak365:', staking365Contract.address)

      try {
        signAddress = await signer.getAddress()
        console.log('[initialStaking] signAddress:', signAddress)
        approveBtn.disabled = false
        withdrawBtn.disabled = false
        emergencyWithdrawBtn.disable = false
      } catch (e) {
        approveBtn.disabled = true
        withdrawBtn.disabled = true
        emergencyWithdrawBtn.disable = true
        signAddress = null
        console.log('Error, no account connected')
        return false
      }

      // balance
      let tokenContract = new ethers.Contract(
        smartContract.bscToken,
        TokenArtifact.abi,
        provider,
      )
      let balance_ = await tokenContract.balanceOf(signAddress)
      legioBalance.innerText = 'Balance: ' + ethers.utils.formatEther(balance_) + ' LGO' // Balance: 0.00 LGO
    } else {
      // console.log('Ethereum object not exist')
      // console.log('Please install MetaMask!')
      // claimAvailableP0.innerText = 'Not Available'
      // claimBtnP0.onclick = null
      // claimBtnP0.disabled = true
    }

  } catch (err) {
    console.log(err)
  }
  return true
}

const selectStaking30 = async () => {
  console.log('select staking 30')
  const _provider = new ethers.providers.Web3Provider(window.ethereum)
  const signer = await _provider.getSigner(0)
  const signAddress = await signer.getAddress()
  // deposits
  if (signAddress !== null) {
    const deposits = await staking30Contract.userDeposits(signAddress);
    let stakedAmount = deposits[0].toString()
    console.log('staked: ', stakedAmount)
    userStaked.innerText = 'Staked: ' + ethers.utils.formatEther(stakedAmount) + ' LGO' // Staked: 0 LGO
  }
  // check allowance
  let tokenContract = new ethers.Contract(smartContract.bscToken, TokenArtifact.abi, _provider,)
  console.log('tokenContract:', tokenContract.address)
  let allowance = await tokenContract.connect(signer).allowance(signAddress, smartContract.staking30)
  console.log('allowance:', ethers.utils.formatEther(allowance))
  if (!allowance.eq(0)){
    // No need to do approve anymore
    approveBtn.innerText = 'Stake'
  } else {
    approveBtn.innerText = 'Approve'
  }
}

