const onApprove = async () => {
  console.log('approve button click... amount:', approveAmount.value) // typeof approveAmount.value is string
  try {
    const { ethereum } = window;
    if (ethereum) {
      const _provider = new ethers.providers.Web3Provider(window.ethereum)
      const signer = await _provider.getSigner(0)
      const signAddress = await signer.getAddress()

      let tokenContract = new ethers.Contract(smartContract.bscToken, TokenArtifact.abi, _provider,)
      let approveReceipt, stakeReceipt, allowance
      const approvalWei = ethers.utils.parseEther(approveAmount.value);
      if (stakingPeriod === 30) {
        allowance = await tokenContract.connect(signer).allowance(signAddress, smartContract.staking30)
        console.log('allowance:', ethers.utils.formatEther(allowance))
        if (allowance > approvalWei){
          // No need to do approve anymore
        } else {
          console.log('start re-approve:', approveAmount.value)
          if (!allowance.eq(0)) {
            console.log('allowance !=0, reset to 0 first')
            approveReceipt = await tokenContract.connect(signer).approve(
              smartContract.staking30, ethers.utils.parseEther('0'), { from: signAddress })
          }
          approveReceipt = await tokenContract.connect(signer).approve(
            smartContract.staking30, approvalWei.mul(10000), { from: signAddress })
          console.log('end re-approve:', approveAmount.value)
        }
        stakeReceipt = await staking30Contract.connect(signer).stake(approvalWei)
        console.log('after stake30:', stakeReceipt)
      } else if (stakingPeriod === 90) {
        allowance = await tokenContract.connect(signer).allowance(signAddress, smartContract.staking90)
        console.log('allowance:', ethers.utils.formatEther(allowance))
        if (allowance > approvalWei){
          // No need to do approve anymore
        } else {
          console.log('start re-approve:', approveAmount.value)
          if (!allowance.eq(0)) {
            console.log('allowance !=0, reset to 0 first')
            approveReceipt = await tokenContract.connect(signer).approve(
              smartContract.staking90, ethers.utils.parseEther('0'), { from: signAddress })
          }
          approveReceipt = await tokenContract.connect(signer).approve(
              smartContract.staking90, approvalWei.mul(10000), { from: signAddress })
          console.log('end re-approve:', approveAmount.value)
        }
        stakeReceipt = await staking90Contract.connect(signer).stake(approvalWei)
        console.log('after stake90:', stakeReceipt)
      } else if (stakingPeriod === 365) {
        allowance = await tokenContract.connect(signer).allowance(signAddress, smartContract.staking365)
        console.log('allowance:', ethers.utils.formatEther(allowance))
        if (allowance > approvalWei){
          // No need to do approve anymore
        } else {
          console.log('start re-approve:', approveAmount.value)
          if (!allowance.eq(0)) {
            console.log('allowance !=0, reset to 0 first')
            approveReceipt = await tokenContract.connect(signer).approve(
              smartContract.staking365, ethers.utils.parseEther('0'), { from: signAddress })
          }
          approveReceipt = await tokenContract.connect(signer).approve(
              smartContract.staking365, approvalWei.mul(10000), { from: signAddress })
          console.log('end re-approve:', approveAmount.value)
        }
        stakeReceipt = await staking365Contract.connect(signer).stake(approvalWei)
        console.log('after stake365:', stakeReceipt)
      } else {
        console.log('[approve] Unsupport stake period:', stakingPeriod)
      }
    } else {
      // console.log('Ethereum object not exist')
      // console.log('Please install MetaMask!')
    }
  } catch (err) {
    console.log(err)
  }
  return true
}

const onWithdraw = async () => {
  try {
    const { ethereum } = window;
    if (ethereum) {
      const _provider = new ethers.providers.Web3Provider(window.ethereum)
      const signer = await _provider.getSigner(0)
      const signAddress = await signer.getAddress()

      let withdrawReceipt
      if (stakingPeriod === 30) {
        withdrawReceipt = await staking30Contract.connect(signer).withdraw()
      } else if (stakingPeriod === 90) {
        withdrawReceipt = await staking90Contract.connect(signer).withdraw()
      } else if (stakingPeriod === 365) {
        withdrawReceipt = await staking365Contract.connect(signer).withdraw()
      } else {
        console.log('[withdraw] Unsupport stake period:', stakingPeriod)
      }
    } else {
      // console.log('Ethereum object not exist')
      // console.log('Please install MetaMask!')
    }
  } catch (err) {
    console.log(err)
  }
  return true
}

const onEmergencyWithdraw = async () => {
  try {
    const { ethereum } = window;
    if (ethereum) {
      const _provider = new ethers.providers.Web3Provider(window.ethereum)
      const signer = await _provider.getSigner(0)
      const signAddress = await signer.getAddress()

      let withdrawReceipt
      if (stakingPeriod === 30) {
        withdrawReceipt = await staking30Contract.connect(signer).emergencyWithdraw()
      } else if (stakingPeriod === 90) {
        withdrawReceipt = await staking90Contract.connect(signer).emergencyWithdraw()
      } else if (stakingPeriod === 365) {
        withdrawReceipt = await staking365Contract.connect(signer).emergencyWithdraw()
      } else {
        console.log('[withdraw] Unsupport stake period:', stakingPeriod)
      }
    } else {
      // console.log('Ethereum object not exist')
      // console.log('Please install MetaMask!')
    }
  } catch (err) {
    console.log(err)
  }
  return true
}


function Staking() {
  const [lockinPeriod, setLockinPeriod] = useState("30 Days");
  const [unstakeFee, setUnstakeFee] = useState("10%");
  const [registratioPercentage, setRegistratioPercentage] = useState("5%");

  const handleChange = (period, unstake, registration) => {
    setLockinPeriod(period);
    setUnstakeFee(unstake);
    setRegistratioPercentage(registration);
    if (period === '30 Days') {
      if (stakingPeriod === 30)
        return
      stakingPeriod = 30
      console.log('Click 30')
      selectStaking30()
    } else if (period === '90 Days') {
      if (stakingPeriod === 90)
        return
      stakingPeriod = 90
      console.log('Click 90')
      selectStaking90()
    } else {
      if (stakingPeriod === 365)
        return
      stakingPeriod = 365
      console.log('Click 1 year')
      selectStaking365()
    }
  };

  const location = useLocation();
  useEffect(() => {
    console.log('[staking] location change:', location.pathname)
    initialStaking()
  }, [location]);

  //  componentDidMount  componentDidUpdate:
  useEffect(() => {
    console.log('[staking] componentDidMount')
    approveAmount = document.getElementById('approveAmount')
    approveBtn = document.getElementById('stakingApprove')
    withdrawBtn = document.getElementById('withdrawBtn')
    emergencyWithdrawBtn = document.getElementById('emergencyWithdrawBtn')
    legioBalance = document.getElementById('legioBalance')
    stakingAPY = document.getElementById('stakeAPY')
    userStaked = document.getElementById('userStaked')
    //console.log('[staking] claimAllocationP0:', claimAllocationP0)
  });

  return (
    <div className="staking" id="staking" data-aos="fade-up">
      <div className="stakingContent">
        <div className="text-start">
          <div className="heading-div">
            <span className="light-heading">Staking</span>
            <p className="subHeading text-white">Staking</p>
          </div>
          <p className="text-grey">Participant IDO Stake</p>
        </div>
        <hr />
        <div className="stakingCard">
          <p className="h2">0 LGO</p>
          <p className="h4">Total Stake</p>
          <hr />
          <div className="btn-group d-flex justify-content-center">
            <button
              onClick={() => handleChange("30 Days", "10%", "5%")}
              className={lockinPeriod === "30 Days" ? "active-btn" : ""}
            >
              30 Days
            </button>
            <button
              onClick={() => handleChange("90 Days", "20%", "20%")}
              className={lockinPeriod === "90 Days" ? "active-btn" : ""}
            >
              90 Days
            </button>
            <button
              onClick={() => handleChange("1 Year", "30%", "100%")}
              className={lockinPeriod === "1 Year" ? "active-btn" : ""}
            >
              1 Year
            </button>
          </div>
          <div>
            <div className="d-flex justify-content-between">
              <p>
                Lock Period: <span>{lockinPeriod}</span>
              </p>
              <p>APR Rate</p>
            </div>
            <div className="d-flex justify-content-between">
              <p>
                Re-looks on registration: <span>Yes</span>
              </p>
              <p id="stakeAPY" style={{ color: "#DB3121", fontWeight: "bold" }}>
                {registratioPercentage}
              </p>
            </div>
            <div className="d-flex justify-content-between">
              <p>
                Early unstake fee: <span>{unstakeFee}</span>
              </p>
              <p>*APR is</p>
            </div>
            <div className="d-flex justify-content-between">
              <p>
                Status: <span>Unlocked</span>
              </p>
              <p>dynamic</p>
            </div>
            <div>
              <p id="legioBalance">
                Balance: <span>0.00 LGO</span>
              </p>
            </div>
            <div className="row g-0 staking-input">
              <div className="col-md-8">
                <input type="text" id="approveAmount" className="form-control" />
              </div>
              <div className="col-md-4 text-center">
                <button id="stakingApprove" onClick={onApprove}>Approve</button>
              </div>
            </div>
            <div className="mt-3">
              <p id="userStaked">
                Staked: <span>0.00 LGO</span>
              </p>
            </div>
            <div className="row my-3 g-0 staking-input">
              <div className="col-md-8">
                <input type="text" className="form-control" />
              </div>
              <div className="col-md-4  text-center">
                <button id="withdrawBtn" onClick={onWithdraw}>Withdraw</button>
              </div>
            </div>
          </div>
          <div className="text-center">
            <button id="emergencyWithdrawBtn" onClick={onEmergencyWithdraw} style={{ color: "black" }}>Emergency Withdrawal</button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Staking;
