

import { useWeb3React } from '@web3-react/core'
import { InjectedConnector } from '@web3-react/injected-connector'

import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import Logo from "../../assets/icons/img/new.png";
import "./navbar.css";
import { useLocation } from "react-router-dom";

export const injectedConnector = new InjectedConnector({
  supportedChainIds: [
      1, // Mainet
      3, // Ropsten
      4, // Rinkeby
      5, // Goerli
      42, // Kovan
      56, // BSC
  ],
})

function Navbar() {
  const [activeTab, setActiveTab] = useState("home");
  const [showMenu, setShowMenu] = useState(false);
  const [isMobileView, setIsMobileView] = useState(false);
  const [isClaimRoute, setIsClaimRoute] = useState(false);
  const location = useLocation();

  useEffect(() => {
    if (location.pathname === "/claim") {
      setIsClaimRoute(true);
    }
    if (location.hash) {
      scrollIntoView(location.hash.substring(1));
    }
  }, [location]);

  useEffect(() => {
    if (window.innerWidth < 800) setIsMobileView(true);
    else setIsMobileView(false);
    window.addEventListener("resize", function () {
      if (window.innerWidth < 800) setIsMobileView(true);
      else setIsMobileView(false);
    });
  }, [window.innerWidth]);

  const scrollIntoView = (id) => {
    console.log('[scrollIntoView] ', id);
    document.getElementById(id).scrollIntoView({
      behavior: "smooth",
    });
  };

  // Thomas: connect wallet
  const { chainId, account, active, activate, deactivate } = useWeb3React()
  const [btnTextMetamask, setBtnTextMetamask] = useState("MM_Connect");
  const [btnTextTrustWallet, setBtnTextTrustWallet] = useState("TW_Connect");
  const [btnTextSafePal, setBtnTextSafePal] = useState("SP_Connect");

  const [metamaskSelect, setMetamaskSelect] = useState(false);
  const [trustWalletSelect, setTrustWalletSelect] = useState(false);
  const [safePalSelect, setSafePalSelect] = useState(false);

  // useEffect(() => {
  //   console.log(active, '- Has changed')
  // },[active]) // <-- here put the parameter to listen
  useEffect(() => {
    console.log('metamaskSelect Has changed:', metamaskSelect)
    if (metamaskSelect && active)
      setBtnTextMetamask('Connected')
  },[metamaskSelect])

  useEffect(() => {
    console.log('trustWalletSelect Has changed:', trustWalletSelect)
    if (trustWalletSelect && active)
      setBtnTextTrustWallet('Connected')
  },[trustWalletSelect])

  useEffect(() => {
    console.log('safePalSelect Has changed:', safePalSelect)
    if (safePalSelect && active)
      setBtnTextSafePal('Connected')
  },[safePalSelect])

  async function handleConnect() {
    console.log('[handleConnect] start')
    // const onboardButton = document.getElementById('MetaMaskBtn')
    // console.log('[handleConnect] onboardButton:', onboardButton)
    try {
      await activate(injectedConnector)
    } catch (ex) {
      console.log(ex)
    }
  }

  async function handleMetamask() {
    await handleConnect()
    setMetamaskSelect(true)
    setTrustWalletSelect(false)
    setSafePalSelect(false)
    console.log('metamaskSelect == true')
  }

  async function handleTrustWallet() {
    await handleConnect()
    setMetamaskSelect(false)
    setTrustWalletSelect(true)
    setSafePalSelect(false)
    console.log('metamaskSelect == false')
  }

  async function handleSafePal() {
    await handleConnect()
    setMetamaskSelect(false)
    setTrustWalletSelect(false)
    setSafePalSelect(true)
    console.log('metamaskSelect == false')
  }
  


  return (
    <div className={"navbar"}>
      <div className={"navbarFlex"}>
        <div className={"brand"}>
          <img className="img-fluid" src={Logo} alt="" />
        </div>

        {isMobileView ? (
          <i
            onClick={() => setShowMenu((prev) => !prev)}
            className="fa-solid fa-bars"
            style={{ color: "white", fontSize: "26px", cursor: "pointer" }}
          ></i>
        ) : (
          <div className={"navigation"}>
            <ul>
              <li
                onClick={() => {
                  scrollIntoView("hero");
                  setActiveTab("home");
                }}
                className={activeTab === "home" ? "active" : ""}
              >
                {/* <Link to="/#staking">Home</Link> */}
                Home
              </li>
              <li
                onClick={() => {
                  scrollIntoView("staking");
                  setActiveTab("staking");
                }}
                className={activeTab === "staking" ? "active" : ""}
              >
                Staking
              </li>
              <li>
                <Link to="/claim">Claim</Link>
              </li>
              <li
                onClick={() => {
                  scrollIntoView("contact");
                  setActiveTab("contact");
                }}
                className={activeTab === "contact" ? "active" : ""}
              >
                Contact
              </li>
              <li data-bs-toggle="modal" data-bs-target="#exampleModal">
                Connect
              </li>
            </ul>
          </div>
        )}
      </div>
      <div
        className={"mobileMenu"}
        style={showMenu ? { top: "64px" } : { top: "-400px" }}
      >
        <ul>
          <li
            onClick={() => {
              setShowMenu(false);
              scrollIntoView("hero");
              setActiveTab("home");
            }}
            // className={activeTab === "home" ?"active" :''}
          >
            Home
          </li>
          <li
            onClick={() => {
              scrollIntoView("staking");
              setShowMenu(false);
              setActiveTab("staking");
            }}
            // className={activeTab === "staking" ?"active" :''}
          >
            Staking
          </li>
          <li>
            <Link to="/claim">Claim</Link>
          </li>
          <li
            onClick={() => {
              scrollIntoView("contact");
              setShowMenu(false);
              setActiveTab("contact");
            }}
            // className={activeTab === "contact" ?"active" :''}
          >
            Contact
          </li>
          <li
            data-bs-toggle="modal"
            data-bs-target="#exampleModal"
            onClick={() => {
              setShowMenu(false);
              setActiveTab("connect");
            }}
            // className={activeTab === "connect" ?"active" :''}
          >
            Connect
          </li>
        </ul>
      </div>

      <div
        className="modal fade"
        id="exampleModal"
        tabIndex="-1"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-body">
              <div className="d-flex align-items-center justify-content-between">
                <div>
                  <h5 className="modal-title" id="exampleModalLabel">
                    Connect Wallet
                  </h5>
                  <p>Please select a wallet to connect to this marketplace</p>
                </div>
                <i
                  className="fa-solid fa-xmark"
                  type="button"
                  // class="btn-close text-white"
                  data-bs-dismiss="modal"
                  aria-label="Close"
                ></i>
              </div>
              <div>
                <ul className="connect-modal-ul">
                  <li>
                    Metamask <i className="fa-solid fa-angle-right"></i>
                    <button type="button" id="MetaMaskBtn" className="wallet-content" onClick={handleMetamask}>
                      {btnTextMetamask}
                    </button>
                    {active && metamaskSelect ? 
                      (<div> </div>) : (null)
                    }
                  </li>
                  <li>
                    Trust Wallet <i className="fa-solid fa-angle-right"></i>
                    <button type="button" id="TrustWalletBtn" className="wallet-content" onClick={handleTrustWallet}>
                      {btnTextTrustWallet}
                    </button>
                    {active && trustWalletSelect ?
                      (<div> </div>) : (null)
                    }
                  </li>
                  <li>
                    SafePal <i className="fa-solid fa-angle-right"></i>
                    <button type="button" id="SafePalBtn" className="wallet-content" onClick={handleSafePal}>
                      {btnTextSafePal}
                    </button>
                    {active && safePalSelect ?
                      (<div> </div>) : (null)
                    }
                  </li>
                </ul>
              </div>
              <p>
                By connecting your wallet, you agree to Terms of Service and our
                Privacy Policy
              </p>{" "}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Navbar;
