import React from "react";
import { Data } from "./TokenData";
import "./tokens.css";
function Tokens() {
  return (
    <div className="token" data-aos="fade-up" id="token">
      <div className="heading-div">
        <span className="light-heading">DeFi Token Utility</span>
        <p className="tokenHeadline subHeading">DeFi Token Utility</p>
      </div>
      <div className="tokensFlex row g-2">
        {Data &&
          Data.map((token, index) => {
            return (
              <div className="tokenCard col-md-4" key={index}>
                <i className={token.icon}></i>
                <p className="tokenTitle text-center">{token.title}</p>
                <p className="tokenInfo text-center">{token.info}</p>
              </div>
            );
          })}
      </div>
    </div>
  );
}

export default Tokens;
